* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-131921: #131921;
  --color-3b4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-bf4800: #bf4800;
  --color-wa2555: rgb(255, 255, 255);
}

body {
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  display: inline-block;
}

.form-control:focus {
  box-shadow: none;
  border-color: inherit;
}

.gap-10 {
  gap: 15px;
}

.gap-15 {
  gap: 15px;
}

.gap-30 {
  gap: 30px;
}

.button {
  background-color: var(--color-232f3e);
  color: var(--color-wa2555);
  font-size: 14px;
  padding: 13px 33px;
  border-radius: 20px;
}

.button:hover {
  background-color: var(--color-febd69);
  color: var(--color-131921);
}

/*------- Header Part -----------*/

.header-top-strip {
  border-bottom: 1px solid var(--color-3b4149);
  background-color: var(--color-131921);
}

.header-upper {
  background-color: var(--color-131921);
}

.header-bottom {
  background-color: var(--color-232f3e);
}

.header-upper .input-group-text {
  background-color: var(--color-febd69);
}

.header-upper.header-upper-links img{
  width: 30px;
  height: 30px;
}

.header-bottom .menu-links a,
.header-bottom .dropdown button  {
  color: var(--color-wa2555);
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: 0.3;
  text-transform: uppercase;
}

.header-bottom .dropdown button:focus {
  box-shadow: none;
}

.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0px) !important;
}

.header-bottom .dropdown-item {
  padding: 20px;
  margin-bottom: 3px;
  border-bottom: 1px solid var(--color-3b4149);
}

.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69) !important;
}
/*--------- Header Part End Here ------------*/





/*------------- Footer Part --------------*/

footer {
  background-color: var(--color-232f3e);
}

/* footer:not(:first-child) {
  border-top: 3px solid 3px solid rgba(255, 255, 255, 0.1);
} */

footer .input-group-text {
  background-color: var(--color-232f3e);
  color: rgb(255, 255, 255);
  padding: 8px 17px;
}


/*-------- Footer Part Ends Here ---------*/


/*------------Home Page-------------*/

.img-fluid {
  font-size: 100;
}

.logos {
  font-size: 50px;
}

.main-banner-content {
  top: 15%;
  left: 5%;
  margin-top: 70%;
}

.main-banner-content h4 {
  font-size: 16px;
  font-weight: 800;
  line-height: 24px;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
  text-transform: uppercase;
}

.main-banner-content h5 {
  font-size: 32px;
  font-weight: 900;
  line-height: 54px;
  margin: 0 0 12px;
  letter-spacing: -2px;
  text-transform: none;
}

.main-banner-content p {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.4;
  color: var(--color-131921);
}


/*---------------small--------------*/

.small-banner-content {
  top: 25%;
  left: 10%;
